import { Form } from "antd";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import { LoginModel, LoginResponseModel } from "../../models/authModels";
import { AuthService } from "../../services/authService";
import { MyFormItem } from "../core/Input";
import { MyBox } from "../core/MyBox";
import { MyButton } from "../core/MyButton";

export default function LoginComponent() {
    let navigate = useNavigate();
    const [userName, setUserName] = useState('');
    const [password, setPassword] = useState('');
    const [isLoading, setIsLoading] = useState(false);

    function handleLogin() {
        setIsLoading(true);
        AuthService.CheckLogin(new LoginModel(userName, password), (data: any) => {
            new LoginResponseModel(data);
            navigate('/');
        }, undefined, () => {
            setIsLoading(false);
        });
    }

    return (
        <MyBox
            title="Đăng nhập"
            isLoading={isLoading}>
            <Form
                layout="vertical"
                onSubmitCapture={handleLogin}>
                <MyFormItem.Input
                    label="Tên đăng nhập"
                    value={userName}
                    setValue={setUserName} />

                <MyFormItem.Password
                    label="Mật khẩu"
                    value={password}
                    setValue={setPassword} />

                <MyButton
                    htmlType="submit"
                    value="Đăng nhập"
                    type="primary" />
            </Form>
        </MyBox>
    );
}